.images {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}

@media (max-width: 700px) {
  .images {
    flex-direction: column;
    align-items: center;
  }

  .images img {
    max-width: 80%;
    height: 170px;
    margin-bottom: 10px;
  }
}